//  See: https://www.labnol.org/internet/embed-responsive-google-maps/28333/
.__ixr[google-maps] {
    position: relative;
    padding-bottom: 75%; // This is the aspect ratio
    height: 0;
    overflow: hidden;
    margin-top: 100px;
    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100% !important;
        height: 100% !important;
    }
}